.date-select{
    background-color: #000000;
    position: relative;
    height: 2rem;
    .start {
        height: 2em;
        left:0px;
        width: 10rem;
        position: absolute;
        background-color: #ccccff;
        text-align: center;
        line-height: 2em;
    }

    .end {
        height: 2em;
        text-align: center;
          line-height: 2em;
                  top: 0px;
            right: 0px;
            width: 10rem;
            position: absolute;
        background-color: #ccccff;
          }
          .svg-container {
            position: absolute;
            left: 10rem;
            right: 10rem;
            top: 0;
            height: 2em;
          }

          svg {
            width: 100%;
            height: 100%;

            rect {
                fill: hsla(230deg,100%,50%,1);
                height: 100%;
                width: 20px;
                top: 0;
                
            }
          }
          .popup-details{
            position: absolute;
            left: 50px;
            top: 200px;
            height: auto;
            border: 2px solid black;
            display: none;
            z-index: 100;
            background-color: #ffffff;
            min-width: 300px;
          }

           .popup-details.show {
            display: block;
            img {
              width: 128px;
            }
           }
}